<script>
    export default {
        name: 'Home',
        props: {
            msg: String
        },
        data() {
            return {
                pro:"",
                po:"",
                post:[],
                slides:0,
                usercom: "",
                iddot1: "",
                liked: "",
                active: 1,
                comid: "",
                iddot: "",
                cats: [],
                image1: require('../components/images/activity.png'),
                image2: require('../components/images/like-image-color.png'),
                temp: "",
                like: 0,
                t: require('../components/images/activity.png'),
                isFavorite: false,
                usercomments:[]
            }
        },
        mounted() {
            let email=sessionStorage.getItem("email")
            fetch("https://secret-ridge-70355.herokuapp.com/api/user/getuser/" + email)
            .then(response => response.json())
            .then(user => {
                this.pro = user.profile
            });
            let postuemail=sessionStorage.getItem("postuser");
            let imageid=sessionStorage.getItem("imageid");
            sessionStorage.setItem('postuser', "")
            sessionStorage.setItem('imageid', "")
            let pa=[]
            fetch("https://secret-ridge-70355.herokuapp.com/api/user/getuser/" + postuemail)
            .then(response => response.json())
            .then(user => {
                this.po = JSON.parse(user.posts);
                this.po.forEach(post=>{
                    if(post.id==imageid){
                        pa=[]
                        this.slides=0
                        post.path.forEach(p=>{
                            pa.push(p.title)
                            this.slides=this.slides+1
                        })
                        var sam={
                            profile:user.profile,
                            name:user.uname,
                            path:pa,
                            comment:post.comment,
                            likes:post.likes,
                            date:post.date,
                            id:post.id,
                            slides:this.slides,
                            moboremail:user.moboremail
                        }
                        post.ucom.forEach(u=>{
                            if(u!=""){
                                this.usercomments.push(u)
                            }
                        })
                        console.log(this.usercomments)
                        this.post.push(sam);
                    }
                })
            });
            console.log(this.post)
        },
        methods: {
            saveucom(img, email) {
                console.log(email)
                let newposts = []
                var com = {
                    uname: this.username,
                    comment: this.usercom
                }
                let ncom = [];
                this.usercom = ""
                this.po.forEach(post => {
                    if (img == post.id) {
                        ncom.push(post.ucom)
                        ncom.push(com)
                        var npost = {
                            id: post.id,
                            path: post.path,
                            comment: post.comment,
                            likes: post.likes,
                            date: post.date,
                            ucom: ncom
                        }
                        newposts.push(npost)
                    } else {
                        newposts.push(post)
                    }
                })
                console.log(newposts);
                fetch("https://secret-ridge-70355.herokuapp.com/api/user/changepost/" + email, {
                        method: "POST",
                        body: JSON.stringify(
                            newposts
                        ),
                        headers: {
                            "Content-type": "application/json; charset=UTF-8"
                        }
                    })
                    .then(response => response.json())
                    .then(json => console.log(json));
            },
            jump(index) {
                this.active = index
            },
            fullcom(id) {
                this.iddot = id
                this.comid = id
            },
            mydislike(img, likes, email) {
                let newposts = []
                this.iddot1 = ""
                this.liked = ""
                console.log(img)
                console.log(likes)
                likes = parseInt(likes);
                likes = likes - 1;
                if (likes < 0) {
                    likes = 0
                }
                likes = likes.toString();
                this.po.forEach(post => {
                    if (img == post.id) {
                        var npost = {
                            id: post.id,
                            path: post.path,
                            comment: post.comment,
                            likes: likes,
                            date: post.date,
                            ucom: post.ucom
                        }
                        newposts.push(npost)
                    } else {
                        newposts.push(post)
                    }
                })
                console.log(newposts);
                fetch("https://secret-ridge-70355.herokuapp.com/api/user/changepost/" + email, {
                        method: "POST",
                        body: JSON.stringify(
                            newposts
                        ),
                        headers: {
                            "Content-type": "application/json; charset=UTF-8"
                        }
                    })
                    .then(response => response.json())
                    .then(json => console.log(json));
            },
            myFunction(img, likes, email) {
                console.log(email);
                let newposts = []
                this.iddot1 = img
                this.liked = img
                console.log(img)
                console.log(likes)
                likes = parseInt(likes);
                likes = likes + 1;
                if (likes < 0) {
                    likes = 0
                }
                likes = likes.toString();
                this.po.forEach(post => {
                    if (img == post.id) {
                        var npost = {
                            id: post.id,
                            path: post.path,
                            comment: post.comment,
                            likes: likes,
                            date: post.date,
                            ucom: post.ucom
                        }
                        newposts.push(npost)
                    } else {
                        newposts.push(post)
                    }
                })
                console.log(newposts);
                fetch("https://secret-ridge-70355.herokuapp.com/api/user/changepost/" + email, {
                        method: "POST",
                        body: JSON.stringify(
                            newposts
                        ),
                        headers: {
                            "Content-type": "application/json; charset=UTF-8"
                        }
                    })
                    .then(response => response.json())
                    .then(json => console.log(json));
            }
        }
    }
